.project-heading{
 text-align: center;
 padding: 4rem 0 2rem 0;

 font-size: 35px;
 text-decoration: underline;
 color:rgb(225, 29, 72);
}

.project-container{
    max-width:90%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
}
.project-card{
    background: #1a1919;
    padding: 1.2rem 1rem;
}

.project-card img{
   width: 100%;
}
.project-title{
    color: white;
    padding: 1rem 0;
}
.pro-details p{
  padding-bottom: 1.2rem;
  font-size: 1.1rem;
  text-align: justify;
  height: 110px;
}
.pro-btn{
    display: flex;
    justify-content:space-between;
    padding: .5rem 0;
   
}
.pro-btn .btn{
    padding: 0.5 1rem;
  
 
}

@media screen and (max-width: 820px){
    .project-container{
        max-width: 70%;
        margin: auto;
        grid-template-columns: 1fr;
       
      
     }

     .project-card img{
        width: 100%;
         height: 450px;
     }

     .pro-details p{
        padding-bottom: 1.2rem;
        font-size:20px;
        text-align: justify;
        height: 110px;
      }

     #tectstack{
        color:rgb(225, 29, 72);
        margin-top: 20px;
        font-size: 20px;
        
    }

    .pro-btn{
        display: flex;
        justify-content:space-around;
       
       
    }
    
}


@media screen and (max-width: 420px){
    .project-container{
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }

    #tectstack{
        color:rgb(225, 29, 72);
        margin-top: 20px;
        font-size: 20px;
        
    }

    .pro-btn{
        display: flex;
        justify-content:space-between;
        padding: .5rem 0;
        margin-top: 20px;
       
    }

    .project-card img{
        width: 100%;
         height: 300px;
     }


}


    
