
.maindivnav{
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  padding: 1rem;
  position: sticky;
  top: 0px;
  z-index: 10;
  background-color: black;
  
 
  
}


 .nav-menu{
  display: flex;
 
 
 }

.nav-menu li{
    
   padding:0 1rem;
}
.nav-menu li a {
  font-size: 1.4rem;
  font-weight: 500;
}

.hamburger{
  display: none;
}


@media screen and (max-width:820px){

  .nav-menu{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width:100%;
    height: 100vh;
    background-color: black;

    /* background: right(0,0,0,0,0.9); */
    position: absolute;
    top: 0;
     left:-100%;
    z-index: -3;
    transition: 0.3s;
   

  }

  .nav-menu.active{
    left:0;
     
  }

  .nav-menu li{
    padding:1rem 0 ;
  }

  .nav-menu li a{
    font-size: 1.5rem ;
  }

  .hamburger{
    display:initial;
    flex-direction: row-reverse;
  
    
  }
  .maindivnav{
    width: 100%;
    display: flex;
    margin: auto;
    
  
  }
  .maindivnav>a{
    font-size: 18px;

  }
  
}









