.home ,.about ,.project ,.skill ,.contact,.calender{
    height:fit-content;
    padding: 20px 0px;
    border-bottom: 1px solid rgb(225, 29, 72);;
   

}

@media screen and (max-width: 820px){
    .home ,.about ,.project ,.skill ,.contact,.calender{
        min-height:auto;
        border-bottom: 1px solid rgb(225, 29, 72);
    
    }
}

@media screen and (max-width: 420px){
    .home ,.about ,.project ,.skill ,.contact,.calender{
        min-height:auto;
        border-bottom: 1px solid rgb(225, 29, 72);
    
    }
}